/* -------------------------- side-bar ------------------------------ */

.side-bar {
    width: 300px;
    height: 100vh;
    background-color: #fff;
    position: fixed;
    top: 0;
    z-index: 99;
    /* overflow-y: scroll; */
}

.toggle-sidebar {
    right: -300px;
    transition-duration: .4s;
    box-shadow: none;
}

.toggled-sidebar {
    right: 0;
    box-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.3);
    transition-duration: .4s;
}

.side-btn {
    background-color: #fff;
    width: 50px;
    height: 50px;
    position: absolute;
    left: -50px;
    top: 20vh;
    cursor: pointer;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 23px;
    box-shadow: -5px 0px 10px 0px rgba(0, 0, 0, 0.3)
}

.buy-btn {
    margin: 0 30px 30px;
    padding-top: 30px;
    text-align: center;
}

.buy-btn>a {
    color: #fff;
    width: 100%;
    height: 45px !important;
    line-height: 45px;
    font-size: 15px;
    border: none;
    font-weight: normal;
    letter-spacing: normal;
    text-align: center;
    text-transform: uppercase;
    transition: all 0.3s;
    border-radius: 2px;
    background: #a0ce4e;
    display: block;
}

.buy-btn>a:hover {
    color: #fff;
    text-decoration: none;
    transition: all 0.3s;
    background: #96c346;
}

.policies, .ss-content {
    border-bottom: 1px solid #dddddd;
    padding-bottom: 30px;
    margin: 0 30px 30px;
    font-size: 13px;
    text-align: center;
}

.policies a {
    color: rgba(0, 0, 0, 0.75);
    transition-duration: .4s;
}

.policies a:hover {
    text-decoration: none;
    transition-duration: .4s;
    color: rgba(0, 0, 0, 1);
}

.policies span {
    color: rgba(0, 0, 0, 0.75);
    padding: 0 10px;
    color: #777777;
}

.ss-title {
    font-size: 15px;
    color: #000000;
    text-transform: uppercase;
    text-align: center;
    display: block;
    margin-bottom: 15px;
}

.ss-content a {
    display: inline-block;
    width: 48%;
    height: 30px;
    line-height: 30px;
    font-size: 13px;
    color: #000000;
    border: 1px solid #dddddd;
    font-weight: normal;
    letter-spacing: normal;
    text-align: center;
    text-transform: uppercase;
    transition: all 0.3s;
    border-radius: 2px;
    margin-right: 2%;
    transition-duration: .4s;
}

.test-box a:hover {
    text-decoration: none;
    background: #dddddd;
    color: #000000;
    transition-duration: .4s;
}

.images {
    text-align: center;
}

.images a {
    background: url(https://avada.theme-fusion.com/wp-content/plugins/fusion-style-selector/assets/images/ss_sprite_1x.jpg?version=2) no-repeat top left;
    display: inline-block;
    margin-right: 2px;
    width: 25px;
    height: 25px;
}

.images .bkgd1 {
    background-position: 0px 0;
}

.images .bkgd2 {
    background-position: -28px 0;
}

.images .bkgd3 {
    background-position: -56px 0;
}

.images .bkgd4 {
    background-position: -84px 0;
}

.images .pattern1 {
    background-position: -112px 0;
}

.images .pattern2 {
    background-position: -140px 0;
}

.images .pattern3 {
    background-position: -168px 0;
}

.ss-desc {
    font-size: 13px;
    color: #000000;
    line-height: 20px;
    margin-bottom: 35px;
    display: inline-block;
    text-align: center;
}

.demos{
    width: 100%;
}
.each-demo{
    float: left;
    width: 137px;
    height: 102px;
    border-radius: 2px;
    text-align: center;
    margin-right: 6px;
    margin-bottom: 6px;
}
.each-demo:nth-child(even){
    margin-right: 0;
}
.each-demo img{
    width: 100%;
    height: 100%;
}

.side-contain{
    width: 100%;
    height: 100%;
    overflow-y: scroll
}
