.flip-pane {
  background-color: rgba(255, 255, 255, 0);
  background-image: url('../../avada/main/bg3.jpg');
  background-position: left top;
  background-repeat: repeat;
  padding-top: 65px;
  padding-right: 30px;
  padding-bottom: 30px;
  padding-left: 30px;
}

.flip-pane .col-md-4 {
  min-height: 247px;
  position: relative;
  margin-bottom: 15px;
  perspective: 1000px;
}

.flip-pane .col-md-4 .front-side {
  background-color: rgb(246, 246, 246);
  border-radius: 4px;
  color: rgb(116, 116, 116);
  transition-duration: 0.4s;
  position: absolute;
  display: flex;
  align-items: center;
  flex-direction: column;
  perspective: 1000px;
  padding: 27px 20px;
  filter: blur(.6px);
  height: 100%;
  backface-visibility: hidden;
}

@keyframes flip-front {
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(200deg);
  }
  100% {
    transform: rotateY(180deg);
  }
}

@keyframes flip-back {
  0% {
    transform: rotateY(180deg);
  }
  50% {
    transform: rotateY(-20deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}

.flip-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transform-style: preserve-3d;
  animation: flip-back .6s forwards
}

.flip-pane .row {
  margin-left: 50px;
  margin-right: 50px;
}

.flip-pane .col-md-4 .front-side div {
  height: 64px;
  width: 64px;
  background-color: #333333;
  border-radius: 50%;
  color: #fff;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
  font-weight: 400;
}

.flip-pane .col-md-4 .front-side h2 {
  font-size: 18px;
  color: #333333;
  font-family: museoslab300, Arial, Helvetica, sans-serif;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0px;
  font-style: normal;
  margin-bottom: 9px;
}

.flip-pane .col-md-4 .back-side {
  background-color: rgb(160, 206, 78);
  border-radius: 4px;
  color: rgb(255, 255, 255);
  bottom: 0px;
  position: absolute;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 27px 20px;
  text-align: center;
  filter: blur(.6px);
  height: 100%;
  /* animation: flip-front; */
  transform: rotateY(180deg);
  backface-visibility: hidden;
}

.flip-pane .col-md-4 .back-side h3 {
  color: #eeeded;
  font-size: 14px;
  font-family: museoslab300, Arial, Helvetica, sans-serif;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0px;
  font-style: normal;
  margin-bottom: 9px;
  text-transform: uppercase;
}

.flip-pane .col-md-4 .back-side a {
  margin: 20px auto 0;
  background: #97c04c;
  border-radius: 2px;
  width: 50%;
  padding: 13px 29px;
  line-height: 17px;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0px;
  display: block;
  color: rgba(255, 255, 255, .7);
}

.flip-pane .col-md-4 .back-side a:hover {
  color: rgba(255, 255, 255, .7);
  text-decoration: none;
}

.flip-pane .col-md-4:hover .flip-inner {
  /* transform: rotateY(180deg); */
  animation: flip-front .6s forwards;
  cursor: pointer;
}

