@font-face {
  font-family: museoslab300;
  src: url(./resource/museoslab300.ttf);
}

@font-face {
  font-family: PTSan;
  src: url(./resource/PTC55F.ttf);
}

.header {
  height: 88px;
  display: flex;
  /* justify-content: space-around; */
  align-items: center;
  z-index: 0;
  position: relative;
  transition-duration: .2s;
  padding-left: 10%;
  padding-right: 10%;
}

.logo{
  padding-right: 10%;
}


/* Menu */

.header .main-menu {
  list-style: none;
  display: flex;
  align-items: center;
  height: 100%;
  margin: 0;
  padding-left: 0
}

/* Menu list*/

.header .main-menu>li {
  padding-right: 27px;
  display: inline-block;
  height: 100%;
  position: relative;
}

/* for dropdown menu */

.header .main-menu>li:hover .submenu-normal {
  visibility: visible;
  opacity: 1;
}

.header .main-menu>li:hover .submenu-pane {
  visibility: visible;
  opacity: 1;
}

.submenu-normal li:hover .sub-level2 {
  visibility: visible;
  opacity: 1;
}

/* for list */

.header .main-menu>li>a {
  display: flex;
  align-items: center;
  font-family: museoslab300;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 0px;
  font-style: normal;
  display: flex;
  align-items: center;
  height: 100%;
  transition-duration: .3s;
  color: #333333;
  border-top: 3px solid transparent;
  cursor: pointer;
  text-decoration: none;
}

.header .main-menu>li>a:hover {
  transition-duration: .4s;
  color: #a0ce4e;
  border-color: #a0ce4e;
}

.header li a.active {
  color: #a0ce4e;
  border-color: #a0ce4e;
}

/*within menu*/

.submenu-normal {
  visibility: hidden;
  position: absolute;
  opacity: 0;
  background-color: #edecec;
  min-width: 215px;
  border-top: 3px solid #a0ce4e;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  list-style: none;
  padding-left: 0;
  transition: all 0.3s ease-in-out;
}

.submenu-normal li {
  position: relative;
  cursor: pointer;
  display: flex;
  border-bottom: 1px solid #e3e2e3;
  background-color: #edecec;
}

.submenu-normal li:hover {
  background-color: #f5f5f5;
  transition-duration: .3s
}

.submenu-normal li a {
  padding: 7px 20px;
  padding-top: 9px;
  padding-bottom: 9px;
  padding-left: 20px;
  text-decoration: none;
  color: #333333;
  font-size: 13px;
}

/*pane menu*/

.submenu-pane {
  width: 1100px;
  position: absolute;
  left: -245px;
  visibility: hidden;
  opacity: 0;
  background-color: #edecec;
  border-top: 3px solid #a0ce4e;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  transition: all 0.3s ease-in-out;
}

.submenu-pane .subpane {
  padding-left: 0;
  display: flex;
}

.submenu-pane .subpane>li {
  width: 25%;
  display: block;
  float: none;
  padding: 29px 0;
  border-right: 1px solid transparent;
  background-size: cover;
  border-color: #e3e2e3;
}

.submenu-pane .subpane li h5 {
  padding: 0 34px 15px;
}

.submenu-pane .sub-in-sub {
  list-style: none;
  padding-left: 0;
}

.submenu-pane .sub-in-sub li a {
  display: block;
  padding: 5px 34px;
  padding-top: 7px;
  padding-bottom: 7px;
  border-bottom: none;
  text-decoration: none;
  color: #333333;
  font-weight: 400;
  font-size: 13px;
}

.submenu-pane .sub-in-sub li:hover {
  background-color: #f5f5f5;
  transition-duration: .3s
}

/*each menu*/

#news .submenu-normal {
  right: 0;
}

#pages .submenu-pane {
  left: -457.594px !important;
}

#features .submenu-pane {
  left: -716.422px !important;
}

#elements .submenu-pane {
  left: -795.984px !important;
}

#shop .submenu-pane {
  left: -657.391px !important;
}

#shop .submenu-pane .subpane>li {
  width: 33.3333333333333% !important;
}

#search a {
  border: none !important;
}

#search a:hover {
  border: none !important;
}

/* panel submenu */

.pane-in-sub {
  text-align: center;
  padding: 0 34px;
}

.pane-in-sub p {
  padding-top: 10px;
  text-align: left !important;
  font-size: 13px;
}

.pane-in-sub p a {
  transition-duration: .3s;
  color: #333333;
}

.pane-in-sub p a:hover {
  transition-duration: .4s;
  color: #a0ce4e;
  text-decoration: none
}

/* submenu level 2 */

.sub-level2 {
  visibility: hidden;
  position: absolute;
  opacity: 0;
  background-color: #edecec;
  min-width: 215px;
  border-top: 3px solid #a0ce4e;
  z-index: 1;
  list-style: none;
  padding-left: 0;
  transition: all 0.3s ease-in-out;
  left: 215px;
  top: -3px;
  font-size: 13px;
}

/* Search */

.search-form {
  position: absolute;
  right: 0;
  width: calc(250px + 1.43 * 40px);
  padding: 25px;
  background-color: #edecec;
  border-color: #e3e2e3;
}

.search-form .form-control, .search-form .input-group-text {
  border-radius: 0 !important;
}

.search-form .form-control {
  color: #aaa9a9 !important;
  font-size: 13px !important;
  border: 1px solid #d2d2d2;
}

.search-form .input-group-text {
  background: #000;
  color: #fff;
  cursor: pointer;
  transition-duration: .3s;
}

.search-form .input-group-text:hover {
  background: #a0ce4e;
  transition-duration: .3s;
}

.search-form .form-control:focus {
  outline: none !important;
  box-shadow: none;
  border: 1px solid #d2d2d2;
  transition: all 0.3s ease-in-out;
}

.show-search {
  visibility: visible;
  opacity: 1;
}

.hide-search {
  visibility: hidden;
  opacity: 0;
}

.scroll-btn {
  height: 35px;
  width: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: .8;
  z-index: 100000;
  position: fixed;
  bottom: 0;
  right: 75px;
  background-color: #333;
  font-size: 18px;
  font-weight: 400;
  color: #fff;
  border-radius: 4px 4px 0 0;
  transition-duration: .4s;
}

.scroll-btn:hover {
  transition-duration: .4s;
  background-color: #a0ce4e;
  cursor: pointer;
}

.hide-scroll {
  transition-duration: .4s;
  opacity: 0;
  visibility: hidden;
}

.show-scroll {
  transition-duration: .4s;
  opacity: 1;
  visibility: visible;
}

.fixed-menu {
  position: fixed;
  background-color: rgba(255, 255, 255, 0.97);
  width: 100%;
  opacity: 1;
  transition-duration: .2s;
  z-index: 99;
}