.footer .sliding-bar{
    background-color: #363839;
    border-color: #e9eaee;
    border-top-width: 12px;
    padding-top: 60px;
    padding-bottom: 55px;
    padding-left: 80px;
    padding-right: 80px;
    border-top: 12px solid #e9eaee;

}
.footer .sliding-bar .textwidget p{
    color: #8c8989;
    font-size: 15px;
    padding-top: 24px;


}
.footer .sliding-bar .textwidget strong{
    cursor: pointer;
    color: #bfbfbf

}
.footer .row{
    margin-left: 0 !important;
    margin-right: 0 !important;
}

.footer .sliding-bar .contact p{
    cursor: pointer;
    color: #bfbfbf;
    border-bottom: 1px solid #505152;
    padding: 10px 10px 10px 0px;
    transition-duration: .4s;
}

.footer .sliding-bar .contact p:hover{
    color: #fff;
    transition-duration: .4s;
}

.footer .copyright{
    background-color: #282a2b;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 80px;
    padding-right: 80px;
    z-index: 10;
    position: relative;
    border-top: 1px solid #4b4c4d;
    display: flex;
    align-items: center;
    justify-content: space-between
}

.footer .copyright-notice{
    color: rgba(255,255,255,0.5);
    font-size: 12px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.footer .copyright-notice a {
    color: rgba(255,255,255,0.8);
    transition-duration: .4s

}

.footer .copyright-notice a:hover {
    color:#a0ce4e;
    text-decoration: none;
    transition-duration: .4s
}

.footer .copyright .social-networks a{
    font-size: 18px;
    margin: 0 10px 10px;
    color: #46494a;
    cursor: pointer;
}

.footer .copyright .social-networks a:hover{
    color: #46494a;
    cursor: pointer;
}