/* ---------------------------------------------------------------------------------------------------------------*/

/* slidiing bar */

.sliding-bar {
    background-color: #363839;
    overflow: hidden;
    transition: all .4s ease;
    padding-top: 60px;
    padding-bottom: 55px;
    padding-left: 80px;
    padding-right: 80px;
  }
  
  /* for expaned */
  
  .expaned {
    padding-top: 35px;
    padding-bottom: 35px;
    height: 36vh;
    transition: all .4s ease;
  }
  
  .not-expaned {
    padding-top: 0;
    padding-bottom: 0;
    height: 0;
    transition: all .4s ease;
  }
  
  /* styling sliding bar */
  
  .sliding-bar h4 {
    color: #dddddd;
    line-height: 13px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0px;
    font-style: normal;
    margin: 0 0 28px;
    text-transform: uppercase;
    font-family: PTSan;
  }
  
  .sliding-bar p {
    color: #8c8989;
    text-align: left;
    font-size: 16px;
    font-family: pt;
    font-weight: 400;
    letter-spacing: 0px;
    font-style: normal;
    font-family: PTSan;
    line-height: 1.69;
    z-index: 99;
    position: relative;
  }
  
  .sliding-bar .textwidget {
    width: 90%;
  }
  
  .sliding-bar .recent-work {
    width: 90%;
  }
  
  .sliding-bar a {
    color: #bfbfbf;
    transition-duration: .3s
  }
  
  .sliding-bar a:hover {
    text-decoration: none;
    color: #a0ce4e;
    transition-duration: .3s
  }
  
  .sliding-bar .recent-work a {
    display: inline-block;
    padding: 3px 3px;
  }
  
  .sliding-bar .recent-work a img {
    border-style: none;
    vertical-align: top;
    max-width: 100%;
    height: auto;
    transition: all .2s;
  }
  
  .sliding-bar .recent-work a img:hover {
    opacity: .5;
    cursor: pointer;
  }
  
  .sliding-bar .contact p {
    margin: 0 !important;
  }
  
  .sliding-area {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    z-index: 99999;
    margin: 0;
    transition: all .4s ease;
    z-index: 99;
  }
  
  .fusion-sb-toggle-wrapper {
    order: 2;
    width: 36px;
    height: 36px;
    display: block;
    position: relative;
  }
  
  .fusion-sb-toggle {
    display: block;
    position: relative;
    transition: none;
    pointer-events: auto;
    border-top: 36px solid #363839;
    border-left: 36px solid transparent;
  }
  
  .screen-reader-text {
    border: 0;
    clip: rect(1px, 1px, 1px, 1px);
    -webkit-clip-path: inset(50%);
    clip-path: inset(50%);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute!important;
    width: 1px;
    word-wrap: normal!important;
  }
  
  /* toggle button */
  
  .etoggle::after {
    left: -15px;
    top: -35px;
    color: #ffffff;
    display: block;
    position: absolute;
    content: '+';
    -webkit-font-smoothing: antialiased;
    line-height: normal;
    text-shadow: none;
    transition: none;
    font-weight: bold
  }
  
  .etoggled::after {
    left: -15px;
    top: -35px;
    color: #ffffff;
    display: block;
    position: absolute;
    content: '-';
    -webkit-font-smoothing: antialiased;
    line-height: normal;
    text-shadow: none;
    transition: none;
    font-weight: bold
  }