body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: museoslab300;
  src: url(/avada/static/media/museoslab300.196c0c5e.ttf);
}

@font-face {
  font-family: PTSan;
  src: url(/avada/static/media/PTC55F.991ba283.ttf);
}

.header {
  height: 88px;
  display: -webkit-flex;
  display: flex;
  /* justify-content: space-around; */
  -webkit-align-items: center;
          align-items: center;
  z-index: 0;
  position: relative;
  transition-duration: .2s;
  padding-left: 10%;
  padding-right: 10%;
}

.logo{
  padding-right: 10%;
}


/* Menu */

.header .main-menu {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  margin: 0;
  padding-left: 0
}

/* Menu list*/

.header .main-menu>li {
  padding-right: 27px;
  display: inline-block;
  height: 100%;
  position: relative;
}

/* for dropdown menu */

.header .main-menu>li:hover .submenu-normal {
  visibility: visible;
  opacity: 1;
}

.header .main-menu>li:hover .submenu-pane {
  visibility: visible;
  opacity: 1;
}

.submenu-normal li:hover .sub-level2 {
  visibility: visible;
  opacity: 1;
}

/* for list */

.header .main-menu>li>a {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-family: museoslab300;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 0px;
  font-style: normal;
  display: flex;
  align-items: center;
  height: 100%;
  transition-duration: .3s;
  color: #333333;
  border-top: 3px solid transparent;
  cursor: pointer;
  text-decoration: none;
}

.header .main-menu>li>a:hover {
  transition-duration: .4s;
  color: #a0ce4e;
  border-color: #a0ce4e;
}

.header li a.active {
  color: #a0ce4e;
  border-color: #a0ce4e;
}

/*within menu*/

.submenu-normal {
  visibility: hidden;
  position: absolute;
  opacity: 0;
  background-color: #edecec;
  min-width: 215px;
  border-top: 3px solid #a0ce4e;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  list-style: none;
  padding-left: 0;
  transition: all 0.3s ease-in-out;
}

.submenu-normal li {
  position: relative;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  border-bottom: 1px solid #e3e2e3;
  background-color: #edecec;
}

.submenu-normal li:hover {
  background-color: #f5f5f5;
  transition-duration: .3s
}

.submenu-normal li a {
  padding: 7px 20px;
  padding-top: 9px;
  padding-bottom: 9px;
  padding-left: 20px;
  text-decoration: none;
  color: #333333;
  font-size: 13px;
}

/*pane menu*/

.submenu-pane {
  width: 1100px;
  position: absolute;
  left: -245px;
  visibility: hidden;
  opacity: 0;
  background-color: #edecec;
  border-top: 3px solid #a0ce4e;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  transition: all 0.3s ease-in-out;
}

.submenu-pane .subpane {
  padding-left: 0;
  display: -webkit-flex;
  display: flex;
}

.submenu-pane .subpane>li {
  width: 25%;
  display: block;
  float: none;
  padding: 29px 0;
  border-right: 1px solid transparent;
  background-size: cover;
  border-color: #e3e2e3;
}

.submenu-pane .subpane li h5 {
  padding: 0 34px 15px;
}

.submenu-pane .sub-in-sub {
  list-style: none;
  padding-left: 0;
}

.submenu-pane .sub-in-sub li a {
  display: block;
  padding: 5px 34px;
  padding-top: 7px;
  padding-bottom: 7px;
  border-bottom: none;
  text-decoration: none;
  color: #333333;
  font-weight: 400;
  font-size: 13px;
}

.submenu-pane .sub-in-sub li:hover {
  background-color: #f5f5f5;
  transition-duration: .3s
}

/*each menu*/

