  padding-left: 0;
  transition: all 0.3s ease-in-out;
  left: 215px;
  top: -3px;
  font-size: 13px;
}

/* Search */

.search-form {
  position: absolute;
  right: 0;
  width: calc(250px + 1.43 * 40px);
  padding: 25px;
  background-color: #edecec;
  border-color: #e3e2e3;
}

.search-form .form-control, .search-form .input-group-text {
  border-radius: 0 !important;
}

.search-form .form-control {
  color: #aaa9a9 !important;
  font-size: 13px !important;
  border: 1px solid #d2d2d2;
}

.search-form .input-group-text {
  background: #000;
  color: #fff;
  cursor: pointer;
  transition-duration: .3s;
}

.search-form .input-group-text:hover {
  background: #a0ce4e;
  transition-duration: .3s;
}

.search-form .form-control:focus {
  outline: none !important;
  box-shadow: none;
  border: 1px solid #d2d2d2;
  transition: all 0.3s ease-in-out;
}

.show-search {
  visibility: visible;
  opacity: 1;
}

.hide-search {
  visibility: hidden;
  opacity: 0;
}

.scroll-btn {
  height: 35px;
  width: 48px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  opacity: .8;
  z-index: 100000;
  position: fixed;
  bottom: 0;
  right: 75px;
  background-color: #333;
  font-size: 18px;
  font-weight: 400;
